.mui-container-fluid {
  padding: 0;
}

.mui-select > select:focus {
  height: 32px;
}
.mui-textfield > label {
  width: auto;
  font-size: 12px;
}

.mui-textfield > input {
  height: 32px;
  /*width: 350px;*/
}
.mui-textfield > input:disabled {
  color: #8b8b8b;
}
.mui-textfield > input:focus,
.mui-textfield > textarea:focus {
  border-color: #24d7d1;
  border-width: 2px;
}

.mui-textfield > input:focus ~ label,
.mui-textfield > textarea:focus ~ label {
  color: #24d7d1;
}

.mui-btn {
  padding: 0 150px;
  background-color: #24d7d1;
  height: 50px;
  font-size: 20px;
  color: #ffffff;
}

.mui-btn.mui-btn--flat {
  color: #f5f5f5;
  background-color: #24d7d1;
}

.mui-btn:active,
.mui-btn:focus,
.mui-btn:hover {
  color: #ffffff;
  background-color: #24d7d1;
}

.mui-btn.mui-btn--flat:active,
.mui-btn.mui-btn--flat:focus,
.mui-btn.mui-btn--flat:hover {
  color: #24d7d1;
}

