  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.broker-image img {
  object-fit: cover;
  height: 72px;
}
.broker-info {
  height: 72px;
}
.broker-contact-info {
  height: 28px;
}
.broker-info .mui-btn.mui-btn--flat.button.btn-accent {
  height: 20px;
  line-height: 20px;
  font-size: 10px;
  text-transform: none;
  padding: 0 24px;
  font-weight: 300;
  border: 1px solid #24d7d1;
  margin-bottom: 0;
}

/*** Broker Empty State Styles ***/
.brokers-empty-state {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: #9e9e9e;
}
.brokers-empty-state i {
  font-size: 36px;
  margin-bottom: 8px;
}
.brokers-empty-state p {
  color: #9e9e9e;
  font-size: 14px;
  text-align: center;
}

/*** QTS Rep Card Styles ***/
.qts-rep-title-mobile {
  display: none;
}
.qts-rep-card {
  @extend .broker-card;
  background: #e0e0e0;
}
.qts-rep-image {
  @extend .broker-image;
}

/*** Brokers Top Section Btns ***/
.brokers-top-section-btns .mui-btn.mui-btn--flat.button.btn-accent {
  margin: 0 0 10px 0;
}
.brokers-top-section-btns .mui-btn.mui-btn--flat.button.btn-accent2 {
  margin: 0;
}

@media only screen and (max-width: 1200px) {
  .brokers-bottom-bar {
    height: 248px;
  }
  .brokers-bottom-bar.show-top-ten > div:nth-child(2) {
    height: 404px;
    overflow: auto;
  }
  .broker-card {
    flex-direction: column;
    align-items: center;
  }
  .qts-rep-card {
    height: 100%;
  }
  .broker-image {
    margin-right: 0;
    margin-bottom: 8px;
  }
  .broker-info {
    text-align: center;
  }
}

@media only screen and (max-width: 968px) {
  .brokers-bottom-section .broker-grid {
    grid-template-columns: calc(25% - 12px) calc(25% - 12px) calc(25% - 12px) calc(
        25% - 12px
      );
  }
}

@media only screen and (max-width: 800px) {
  .brokers-bottom-bar {
    height: 384px;
    overflow: auto;
  }
  .brokers-top-section .broker-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .brokers-bottom-section .broker-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .qts-rep-title {
    display: none;
  }
  .qts-rep-title-mobile {
    display: block;
    margin-bottom: 8px;
  }
  .brokers-top-section-btns {
    margin-top: 24px;
  }
}

@media only screen and (max-width: 600px) {
  .brokers-bottom-bar {
    height: auto;
    overflow: hidden;
    bottom: 32px;
  }
  .brokers-bottom-bar > div:nth-child(2) {
    height: 404px;
    overflow: auto;
  }
  .brokers-top-section .broker-grid {
    grid-template-columns: 100%;
  }
  .brokers-bottom-section .broker-grid {
    grid-template-columns: 100%;
  }
  .grid-column-three {
    grid-column-end: 1;
  }
  .brokers-top-section-titles p,
  .qts-rep-title-mobile {
    margin-bottom: 8px;
    font-size: 10px;
  }
  .brokers-bottom-bar-mobile {
    display: block;
    bottom: 0;
    height: 436px;
  }
  .brokers-bar-left p {
    font-size: 12px;
  }
  .brokers-bar-left .mui-btn.mui-btn--flat.button.btn-action {
    display: none;
  }
  .broker-card,
  .qts-rep-card {
    flex-direction: row;
  }
  .broker-image,
  .qts-rep-image {
    margin-right: 32px;
    height: 32px;
    width: 32px;
    min-width: 32px;
  }
  .broker-image img,
  .qts-rep-image img {
    height: 32px;
  }
  .qts-rep-info .xs-text {
    font-size: 10px;
  }
  .brokers-empty-state p {
    font-size: 12px;
  }
  .broker-info {
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: auto;
  }
  .brokers-bottom-bar-mobile .brokers-bar {
    margin-bottom: 16px;
  }
  .brokers-bottom-bar-mobile.collapsed {
    border-top: 1px solid #fff;
  }
}

@media only screen and (max-width: 440px) {
  .broker-info .mui-btn.mui-btn--flat.button.btn-accent {
    min-width: initial;
    padding: 0 8px;
  }
  .broker-image,
  .qts-rep-image,
  .qts-rep-image {
    margin-right: 16px;
  }
}

@media only screen and (max-width: 392px) {
  .broker-info {
    display: block;
  }
}

/***** RATES CARD STYLES *****/
.rates-card {
  position: absolute;
  z-index: 2;
  top: 120px;
  left: 32px;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.5);
  background: #fff;
  width: 328px;
  padding: 16px;
}
.rates-card::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  left: 0;
  top: 344px;
  box-sizing: border-box;
  border: 6px solid black;
  border-color: transparent transparent #fff #fff;
  transform-origin: 0 0;
  transform: rotate(45deg);
  box-shadow: -1px 2px 0px 0 rgba(0, 0, 0, 0.2);
}

/*** Rates Card Header ***/
.rates-card-header {
  margin-bottom: 16px;
}
.rates-card-header i {
  font-size: 18px;
}
.rates-card-header i:hover {
  opacity: 0.5;
  transition: opacity 300ms linear;
  cursor: pointer;
}

/*** Rates Grid ***/
.rate-boxes {
  grid-template: 1fr 1fr 1fr 1fr / calc(50% - 4px) calc(50% - 4px);
  grid-gap: 8px;
}
.rate-box {
  background: #eee;
  display: flex;
  justify-content: space-between;
  padding: 12px;
  font-size: 12px;
  text-align: left;
}
.rates {
  margin-left: 12px;
  text-align: right;
}
.rates span {
  display: block;
}
.rates-card-buttons {
  margin-top: 24px;
}
.rates-card-buttons .mui-btn.mui-btn--flat.button {
  height: 32px;
  width: 144px;
  line-height: 32px;
  font-size: 12px;
}
.rates-card-buttons .mui-btn.mui-btn--flat.button.btn-accent2 {
  border: 1px solid #999;
}
.rates-card-buttons .mui-btn.mui-btn--flat.button.btn-accent {
  border: 1px solid #24d7d1;
}

@media only screen and (max-width: 1000px) {
  .rates-card {
    width: 280px;
  }
  .rates-card-header p {
    font-size: 12px;
  }
  .rates-card-header i {
    font-size: 16px;
  }
  .rate-box {
    padding: 8px;
    font-size: 10px;
  }
}

@media only screen and (max-width: 400px) {
  .rates-card {
    width: 232px;
    left: 16px;
  }
  .rates-card-header {
    margin-bottom: 8px;
  }
  .rate-box {
    font-size: 9px;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /* IE10+ CSS styles go here */
  .rate-box {
    width: calc(50% - 8px);
    margin: 4px;
  }
}

/***** STREET VIEW STYLES *****/
/*** Open Street View Styles ***/
.openStreetView {
  position: fixed;
  z-index: 10;
  top: 120px;
  right: 312px;
  background-color: #ff5252;
  cursor: pointer;
  height: 54px;
  width: 54px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  p {
    position: fixed;
    top: 136px;
    right: 376px;
    background-color: #000;
    opacity: 0.6;
    color: #fff;
    padding: 5px 15px;
    border-radius: 3px;
  }
}

.openStreetViewNoList {
  @extend .openStreetView;
  right: 4px;
  p {
    right: 64px;
  }
}

@media only screen and (max-width: 700px) {
  .openStreetView p {
    display: none;
  }
  .openStreetViewNoList {
    height: 40px;
    width: 40px;
    top: 72px;
    right: 40px;
  }
}

/*** Street View Styles ***/
.comps-street-sub-header {
  @extend .sub-header;
  width: 100%;
  z-index: 2;
}
.comps-back-arrow img {
  height: 24px;
  margin-left: 20px;
  float: left;
  margin-top: 20px;
  cursor: pointer;
}
.address-title {
  color: #fff;
  margin-top: 22px;
  display: inline-block;
  font-size: 16px;
  margin-left: 13px;
}
.streetview-map {
  width: 100% !important;
  height: 93.5% !important;
  z-index: -1;
  margin-top: 60px;
  position: static !important;
}

